.header {
  position: fixed;
  z-index: 100;
  width: 100%;
  background: rgba(0, 0, 0, 0.25);
  backdrop-filter: blur(10px);

  &__nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__part {
    display: flex;
    align-items: center;
    width: 40%;

    &:last-child {
      justify-content: flex-end;
    }

    @include small-desktop {
      width: 45%;
    }

    @include tablet {
      width: auto;
    }
  }

  &__burger {
    margin-right: 134px;

    @include small-desktop {
      margin-right: 104px;
    }

    @include tablet {
      margin-right: 0;
    }
  }

  &__message {
    display: flex;
    align-items: center;

    @include tablet {
      display: none;
    }
  }

  &__text {
    margin-right: 32px;
    font-weight: 300;
    font-size: 13px;
    line-height: 1.2;
  }

  &__logo {
    margin-top: -13px;
    margin-bottom: -10px;

    @include tablet {
      margin-top: -8px;
      margin-bottom: 0;
    }
  }

  &__filters {
    display: flex;
    align-items: center;
    justify-content: flex-end;

    &--header {
      @include tablet {
        display: none;
      }
    }

    &--main {
      display: none;

      @include tablet {
        display: flex;
      }

      @include mobile {
        justify-content: flex-start;
      }
    }
  }

  &__sort {
    &:not(:first-child) {
      margin-left: 43px;

      @include mobile {
        margin-left: 10px;
      }
    }
  }

  &__menu {
    position: absolute;
    top: 100%;
    padding: 38px;
    width: 288px;
    background-color: rgba(#0a0706, 0.9);
    transform: scaleY(0);

    > li {
      > a {
        font-weight: 300;
        font-size: 24px;
        line-height: 1.4;
        letter-spacing: 1.2px;
        text-transform: uppercase;
        color: $accent-color;
        transition: color 0.3s;

        &:hover {
          color: $light-color;
        }

        @include tablet {
          font-size: 15px;
        }
      }

      &:not(:last-child) {
        margin-bottom: 30px;

        @include tablet {
          margin-bottom: 20px;
        }
      }
    }

    &--opened {
      transform: scaleY(1);
    }

    @include tablet {
      padding: 18px;
      width: 188px;
    }
  }

  &__msg {
    &--mobile {
      display: none;
    }

    @include tablet {
      .msg__icon {
        margin-right: 0;
      }

      &--desktop {
        display: none;
      }

      &--mobile {
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        background-color: $dark-accent-color;
      }
    }
  }

  @include tablet {
    padding: 15px 0;
  }
}
