/* stylelint-disable */

@mixin burger {
  position: relative;
  border: none;
  padding: 0;
  width: 37px;
  height: 21px;
  background-color: transparent;
  cursor: pointer;

  &__text {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: calc(100% + 25px);
    font-size: 15px;
    letter-spacing: 0.75px;
    text-transform: uppercase;
    opacity: 0.5;
    color: $light-color;
    transition: opacity 0.3s;

    @include small-desktop {
      left: calc(100% + 15px);
      font-size: 13px;
    }

    @include tablet {
      display: none;
    }
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: $light-color;
    transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;

    @include tablet {
      content: none;
    }
  }

  &::before {
    top: 0;
  }

  &::after {
    top: calc(100% - 3px);
  }

  &__line {
    position: absolute;
    left: 0;
    top: 50%;
    width: 100%;
    height: 3px;
    background-color: $light-color;
    transform: translateY(-50%);
    transition: transform 0.3s ease-in-out;

    @include tablet {
      display: none;
    }
  }

  &:hover {
    .burger__text {
      opacity: 1;
    }
  }

  &--active {
    &::before {
      top: 50%;
      transform: rotate(45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    &::after {
      top: 50%;
      transform: rotate(-45deg);
      transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
    }

    .burger__line {
      transform: scale(0);
      transition: transform 0.3s ease-in-out;
    }
  }

  @include tablet {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-image: url("../img/burger.svg"), linear-gradient(180deg, #e4cf95 27.38%, #ba9356 82.14%);
    background-size: 24px 24px, contain;
    background-repeat: no-repeat;
    background-position: center center;
    background-color: #e4cf95;
  }
}
