.msg {
  display: flex;
  align-items: center;
  color: $light-color;

  &__icon {
    flex-shrink: 0;
    margin-right: 14px;
    width: 18px;
    height: 18px;
    fill: $light-color;
    transition: 0.3s;
  }

  &__text {
    font-weight: 300;
    font-size: 15px;
    letter-spacing: 0.75px;
    transition: 0.3s;

    @include small-desktop {
      font-size: 13px;
    }
  }

  &:hover {
    color: $accent-color;

    .msg__icon {
      fill: $accent-color;
    }
  }
}
