.mainContent {
  padding: 70px 0;

  &__container {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      right: -212px;
      top: -319px;
      z-index: -1;
      border-radius: 500px;
      width: 500px;
      height: 500px;
      background-color: #f06d11;
      opacity: 0.3;
      filter: blur(100px);

      @include mobile {
        content: none;
      }
    }
  }

  &__btns {
    padding-top: 54px;
    text-align: center;

    @include mobile {
      padding-top: 24px;
    }
  }

  @include tablet {
    padding-top: 0;
  }

  @include mobile {
    padding-bottom: 14px;
  }
}
