.hero {
  padding: 204px 0 100px;
  height: 842px;
  text-align: center;
  background: $dark-color url("../img/hero-bg@2x.jpg") no-repeat center center;
  background-size: cover;

  &__wrap {
    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 100%;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 27px;
    font-weight: 400;
    font-size: 70px;
    letter-spacing: 3.5px;
    text-transform: uppercase;

    @include small-desktop {
      font-size: 60px;
    }

    @include mid-tablet {
      font-size: 46px;
    }

    @include mobile {
      margin-bottom: 30px;
      font-size: 40px;
    }
  }

  &__desc {
    margin-top: 0;
    margin-bottom: 72px;
    font-weight: 300;
    font-size: 22px;
    line-height: 1.8;
    letter-spacing: 1.1px;

    p {
      margin-top: 0;
      margin-bottom: 0;
    }

    @include small-desktop {
      margin-bottom: 52px;
      font-size: 18px;
    }

    @include mobile {
      max-width: 248px;
      line-height: 1.6;
      letter-spacing: 0.9px;

      p {
        margin-bottom: 20px;
      }
    }
  }

  &__btn {
    @include mobile {
      margin-top: auto;
    }
  }

  .container {
    @include mobile {
      height: 100%;
    }
  }

  @include small-desktop {
    height: 768px;
  }

  @include tablet {
    padding: 154px 0 60px;
    height: 588px;
  }

  @include mid-tablet {
    padding: 134px 0 60px;
    height: 518px;
  }

  @include mobile {
    padding: 159px 0 23px;
    height: 686px;
    background-image: url("../img/hero-bg-mobile.jpg");
  }
}
