.checkboxes {
  position: relative;

  &__list {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 10;
    overflow: auto;
    border: 1px solid $light-color;
    border-top: none;
    padding: 34px 29px;
    width: 100%;
    max-height: 300px;
    background-color: $dark-color;
    transform: scaleY(0);
    transform-origin: top center;
    transition: transform 0.2s;
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }

  &__label {
    display: flex;
    align-items: center;
    font-weight: 300;
    font-size: 14px;
    letter-spacing: 0.7px;
    color: $light-color;
    transition: 0.3s;
    cursor: pointer;

    &:hover {
      color: $accent-color;
    }

    @include small-desktop {
      font-size: 13px;
    }
  }

  &__input {
    display: none;

    &:checked ~ .checkboxes__field {
      &::before {
        opacity: 1;
      }
    }
  }

  &__field {
    position: relative;
    flex-shrink: 0;
    margin-right: 14px;
    border: 1px solid $light-color;
    width: 14px;
    height: 14px;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 50%;
      top: 50%;
      display: block;
      width: 17px;
      height: 15px;
      background: transparent url("../img/tick.svg") no-repeat center center;
      background-size: contain;
      opacity: 0;
      transform: translate(-50%, -50%);
      transition: 0.3s;
    }
  }

  &__title {
    text-align: left;

    @include small-desktop {
      font-size: 13px;
    }
  }
}
