.btn {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border: 1px solid $accent-color;
  border-radius: 10px;
  padding: 20px;
  height: 60px;
  min-width: 200px;
  font-size: 15px;
  line-height: 1.4;
  letter-spacing: 0.75px;
  text-align: center;
  text-transform: uppercase;
  color: $light-color;
  transition: 0.3s;

  &::before {
    content: "";
    position: absolute;
    left: -100%;
    top: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, transparent, rgba(255, 255, 255, 0.8), transparent);
    transition: all 0.2s ease;
  }

  &:hover {
    color: $dark-color;
    background-color: $accent-color;

    &::before {
      left: 100%;
      transition: all 0.4s ease;
    }
  }

  @include mobile {
    height: 50px;
  }
}
