.form {
  background-color: #000;

  &__title {
    margin-top: 0;
    margin-bottom: 45px;
    font-weight: 300;
    font-size: 36px;
    line-height: 1.4;
    letter-spacing: 1.8px;

    @include small-desktop {
      margin-bottom: 25px;
      font-size: 32px;
    }

    @include mobile {
      font-size: 28px;
    }

    @include small-mobile {
      font-size: 24px;
    }
  }

  &__input {
    border: 1px solid $light-color;
    padding: 10px 15px 10px 24px;
    width: 100%;
    height: 50px;
    font-size: 16px;
    line-height: 1.4;
    letter-spacing: 0.7px;
    color: $light-color;
    background-color: transparent;
    transition: border-color 0.3s;

    &::placeholder {
      font-size: 14px;
      color: $light-color;
    }

    &:focus-visible {
      outline: none;
    }

    &:hover {
      border-color: $accent-color;
    }

    &--withArrow {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        right: 9px;
        top: calc(50% - 12px);
        width: 24px;
        height: 24px;
        background: transparent url("../img/arrow.svg") no-repeat center center;
        background-color: contain;
        transition: 0.3s;
      }
    }

    &--opened {
      &::before {
        transform: rotate(180deg);
      }

      ~ .checkboxes__list {
        transform: scale(1);
      }
    }

    @include small-tablet {
      height: 42px;
      font-size: 14px;
    }
  }

  &__btn {
    min-width: 228px;
  }

  &__grid {
    display: grid;
    gap: 24px;

    &--two-cols {
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: 45px;
      max-width: 480px;

      @include mobile {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }

  &__content {
    padding: 62px 20px 62px 90px;

    @include small-desktop {
      padding: 32px;
    }
  }

  &--search {
    background: #000 url("../img/form-bg.jpg") no-repeat right center;
    background-size: auto 100%;

    @include mid-tablet {
      background-image: none !important;
    }

    @include mobile {
      text-align: center;
    }

    @media only screen and (-webkit-min-device-pixel-ratio: 2),
           only screen and (min-resolution: 192dpi) {
      background-image: url("../img/form-bg@2x.jpg");
    }
  }
}
