.mainSlider {
  position: relative;
  overflow: hidden;
  margin-top: 68px;
  margin-bottom: 68px;

  .swiper-slide {
    width: auto;
  }

  &__slide {
    display: inline-block;
    overflow: hidden;

    &:hover {
      .mainSlider__img {
        transform: scale(1.1);
      }
    }
  }

  &__img {
    width: auto;
    height: 407px;
    transition: 0.3s;

    @include small-desktop {
      height: 257px;
    }

    @include small-tablet {
      height: 187px;
    }
  }

  &__navigation {
    .swiper-pagination {
      display: none;
    }
  }

  @include small-tablet {
    margin-top: 38px;
    margin-bottom: 38px;
  }
}
