.sort {
  position: relative;
  padding-right: 31px;

  &__btn {
    padding: 27px 0;
    font-weight: 300;
    font-size: 15px;
    letter-spacing: 0.75px;
    text-transform: uppercase;
    color: $light-color;
    transition: 0.3s;

    &::after {
      content: "";
      position: absolute;
      right: 0;
      top: calc(50% - 12px);
      display: block;
      width: 24px;
      height: 24px;
      background: transparent url("../img/arrow.svg") no-repeat center center;
      transform-origin: center;
      transition: 0.3s;
    }

    &:hover {
      color: $accent-color;
    }

    &--opened {
      &::after {
        transform: rotate(-90deg);
      }

      ~ .sort__menu {
        transform: scaleY(1);
      }
    }

    @include small-desktop {
      font-size: 13px;
      letter-spacing: 0.65px;

      &::after {
        right: 5px;
      }
    }
  }

  &__menu {
    position: absolute;
    left: 0;
    top: 100%;
    z-index: 10;
    overflow: auto;
    padding: 34px 29px;
    max-width: 288px;
    max-height: 400px;
    background-color: $dark-color;
    transform: scaleY(0);
    transform-origin: top center;

    &--filter {
      left: auto;
      right: 0;
      overflow: auto;
      height: 771px;
      min-width: 288px;
      max-height: calc(100vh - 71px);

      @include small-desktop {
        min-width: 268px;
      }
    }

    @include small-desktop {
      padding: 24px 19px;
    }
  }

  &__close {
    position: absolute;
    right: 9px;
    top: 7px;
    width: 24px;
    height: 24px;

    &:hover {
      .sort__closeIcon {
        stroke: $accent-color;
      }
    }
  }

  &__closeIcon {
    display: block;
    width: 100%;
    height: 100%;
    stroke: $light-color;
    transition: 0.3s;
  }

  &__list {
    &--hidden {
      .sort__item {
        &:nth-child(n + 4) {
          display: none;
        }
      }
    }
  }

  &__item {
    text-wrap: nowrap;

    &:not(:last-child) {
      margin-bottom: 20px;

      @include small-desktop {
        margin-bottom: 14px;
      }
    }
  }

  &__block {
    display: block;

    &:not(:last-child) {
      margin-bottom: 29px;

      @include small-desktop {
        margin-bottom: 22px;
      }
    }
  }

  &__title {
    margin-bottom: 14px;
    font-weight: 600;
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: 0.7px;
    text-transform: uppercase;
    color: $accent-color;
  }

  &__more {
    margin-top: 22px;
    font-weight: 300;
    font-size: 13px;
    line-height: 1.4;
    letter-spacing: 0.65px;
    color: $accent-color;
  }
}
