.girl {
  &__link {
    display: block;
    color: $light-color;

    &:hover {
      .girl__top {
        transform: scale(1.04);
      }
    }
  }

  &__top {
    position: relative;
    display: flex;
    justify-content: center;
    transition: 0.2s;
  }

  &__label {
    position: absolute;
    right: 3px;
    top: 3px;
    display: block;
    padding: 3px 5px;
    font-weight: 300;
    font-size: 12px;
    letter-spacing: 0.6px;
    text-transform: uppercase;
    background-color: $dark-accent-color;
  }

  &__content {
    padding-top: 17px;
    padding-bottom: 14px;
    text-align: center;

    @include small-tablet {
      padding-bottom: 8px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 16px;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.4;
    letter-spacing: 1.8px;
    color: $accent-color;
  }

  &__location {
    display: block;
    margin-bottom: 16px;
    font-size: 14px;
    line-height: 1.4;
    letter-spacing: 0.7px;
  }

  &__price {
    display: block;
    font-weight: 300;
    font-size: 16px;
    line-height: 1.4;
    letter-spacing: 0.8px;
  }

  @include mobile {
    max-width: 320px;
  }
}
