.footer {
  background-color: #000;

  &__top {
    display: flex;
    justify-content: space-between;
    padding: 41px 0 60px;

    @include tablet {
      display: block;
      padding: 30px 0;
    }
  }

  &__col {
    &--menu {
      display: flex;

      @include tablet {
        display: block;
        margin-top: 30px;
        margin-bottom: 30px;
      }
    }
  }

  &__bottom {
    border-top: 1px solid rgba(252, 244, 225, 0.3);
    padding: 18px 0;

    a {
      color: #cecece;
      transition: color 0.3s;

      &:hover {
        color: $accent-color;

        .footer__icon {
          fill: $accent-color;
        }
      }
    }
  }

  &__add {
    display: flex;
    align-items: center;

    @include mobile {
      flex-direction: column;
      align-items: center;
    }
  }

  &__text {
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 1.5;
    color: #cecece;

    @include mobile {
      display: block;
      font-size: 12px;
    }
  }

  &__privacy {
    margin-right: 180px;

    @include tablet {
      margin-right: 40px;
    }

    @include mobile {
      margin-right: 0;
    }
  }

  &__copyright {
    margin-right: 160px;

    @include tablet {
      margin-right: 40px;
    }

    @include mobile {
      margin-right: 0;
    }
  }

  &__sheer {
    display: flex;
    align-items: center;
  }

  &__icon {
    display: block;
    margin-left: 15px;
    width: 72px;
    height: 14px;
    fill: #cecece;
    transition: 0.3s;

    @include mobile {
      margin-left: 10px;
      width: 60px;
      height: 10px;
    }
  }

  &__menu {
    > li {
      > a {
        font-weight: 300;
        font-size: 15px;
        line-height: 2;
        letter-spacing: 0.75px;
        text-transform: uppercase;
        color: $light-color;
        transition: color 0.3s;

        &:hover {
          color: $accent-color;
        }
      }
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      > li {
        > a {
          font-weight: 300;
          font-size: 14px;
          line-height: 1.8;
          letter-spacing: 0.7px;
          color: #fff;
          transition: color 0.3s;

          &:hover {
            color: $accent-color;
          }
        }
      }
    }

    &:first-child {
      margin-right: 147px;

      @include tablet {
        margin-right: 67px;
      }
    }

    &:nth-child(2) {
      margin-right: 126px;

      @include tablet {
        margin-right: 67px;
      }
    }
  }
}
